<template>
<Transition name="modal">
  <div class="modal" @click="closeModal">
    <div class="modal__block" @click.stop="">
      <h2 class="modal__title">{{edit ? $t('changeNote') : $t('addNote')}}</h2>
      <div class="modal__inputs">
        <label>
          <span>Title</span>
          <input type="text" placeholder="Title" v-model="title" />
        </label>
        <label>
          <span>Content</span>
          <textarea type="text" placeholder="Content" v-model="desc"></textarea>
        </label>
      </div>
      <div class="modal__btns">
        <button class="modal__btn cancel" @click="closeModal">{{ $t('cancel') }}</button>
        <button class="modal__btn add" @click="addNote" v-if="!edit">{{ $t('add') }}</button>
        <button class="modal__btn add" @click="changeNote" v-else>{{ $t('change') }}</button>
      </div>
    </div>
  </div>
  </Transition>
</template>

<script>
import {v4 as uuidv4 } from 'uuid'

export default {
  props: {
    edit: Boolean,
    editNote: Object
  },
  data(){
    return {
      title: '',
      desc: '',
    }
  },
  methods: {
    changeNote(){
      if(this.title != '' &&  this.desc != ''){
        const editedNote = {
          id: this.editNote.id,
          title: this.title,
          desc: this.desc,
          date: new Date().toLocaleDateString()
        }
        this.$emit('editedNote', editedNote);
        this.closeModal()
      }
    },
    closeModal(){
      this.$emit('closeModal', false);
      this.title = '';
      this.desc = '';
    },
    addNote(){

      if(this.title != '' &&  this.desc != ''){
        let item = {
          id: uuidv4(),
          title: this.title,
          desc: this.desc,
          date: new Date().toLocaleDateString()
        }
        this.$emit('addNote', item)
        this.title = '';
        this.desc = '';
      }
    }
  }
};
</script>

<style>
.modal {
  background: rgba(0, 0, 0, 0.35);
  position: fixed;
  inset: 0;
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal__block {
  border-radius: 28px;
  background: linear-gradient(
      0deg,
      rgba(103, 80, 164, 0.11) 0%,
      rgba(103, 80, 164, 0.11) 100%
    ),
    #fffbfe;
  max-width: 312px;
  width: 100%;
  padding: 24px;
}

.modal__title {
  color: #1c1b1f;
  font-size: 24px;
  font-weight: 400;
  line-height: 133%;
  margin-bottom: 16px;
}

.modal__inputs {
  display: flex;
  flex-direction: column;
  row-gap: 16px;
  margin-bottom: 24px;
}

.modal__inputs label {
  position: relative;
}

.modal__inputs span {
  position: absolute;
  top: 8px;
  left: 16px;
  color: #6750a4;
  font-size: 12px;
  font-weight: 400;
  line-height: 133%;
  letter-spacing: 0.4px;
}

.modal__inputs input,
.modal__inputs textarea {
  border-radius: 4px 4px 0px 0px;
  background: #e7e0ec;
  width: 100%;
  padding: 23px 16px 9px;
  color: #49454f;
  font-size: 16px;
  font-weight: 400;
  line-height: 150%;
  letter-spacing: 0.5px;
  height: 57px;
  border-bottom: 1px solid #1c1b1f;
}

.modal__btns {
  display: flex;
  justify-content: flex-end;
  column-gap: 8px;
}

.modal__btn {
  padding: 10px 12px;
  border-radius: 5px;
  text-align: center;
  font-size: 14px;
  font-weight: 500;
  line-height: 143%;
  letter-spacing: 0.1px;
  text-transform: uppercase;
  transition: 300ms;
}

.cancel {
  color: #CF1B1B;
}
.cancel:hover {
  background: #FFE1E1;
}

.add {
  color: #6750A4;
}

.add:hover {
  background: #E6DDFF;
}

.modal-enter-active, .modal-leave-active {
  transition: 200ms linear;
}

.modal-enter-from, .modal-leave-to {
  opacity: 0;
  transform: scale(1.5);
}

</style>